<template>

      <div class="flex flex-col items-center justify-center">
        <div class="sm:rounded-sm bg-white shadow p-6 w-105 shadow-lg"><h1
          class="text-center mb-6 font-normal font-body text-2xl">Get Started for Free</h1>
          <div class="mb-6">
            <div>
              <div class="flex flex-wrap justify-between"><label class="pr-2 mb-2">Email<span
                class="text-pink">*</span></label></div>
              <input type="email"
                     class="h-10 flex-1 block w-full border border-gray-400 placeholder-gray-400 rounded-sm mb-4"
                    name="email"  v-model="email"/>
<!--              <div class="flex flex-wrap justify-between"><label class="pr-2 mb-2">Username<span-->
<!--                class="text-pink">*</span></label></div>-->
<!--              <input type="text"-->
<!--                     class="h-10 flex-1 block w-full border border-gray-400 placeholder-gray-400 rounded-sm mb-4"-->
<!--                     name="username"  v-model="username"/>-->
              <div class="flex flex-wrap justify-between"><label class="pr-2 mb-2">Password<span
                class="text-pink">*</span></label></div>
              <input type="password"
                     class="h-10 flex-1 block w-full border border-gray-400 placeholder-gray-400 rounded-sm mb-6"
                     required="" name="password" v-model="password"/>
              <div class="flex flex-wrap justify-between"><label class="pr-2 mb-2">Password Confirm<span
                class="text-pink">*</span></label></div>
              <input type="password"
                     class="h-10 flex-1 block w-full border border-gray-400 placeholder-gray-400 rounded-sm mb-6"
                    name="password_verify" v-model="password_verify"/>
              <button class="inline-flex items-center justify-center font-mono uppercase transition ease-in duration-200 hover:no-underline active:shadow-inner focus:outline-none rounded-sm h-10 px-4 text-sm border text-white shadow hover:shadow-hover hover:border-transparent active:border-transparent bg-black border-black hover:text-pink w-full"
                      @click="register">Sign Up
              </button>
            </div>
            <div></div>
          </div>
          <div class="text-center text-sm">Signing up signifies you have read and agree to the <a
                  @click="toTerms">Terms of Service</a> and <a @click="toPolicy" >Privacy Policy</a></div>
        </div>
        <div class="text-center p-6"><p class="text-white text-sm">Already have an account?<!-- --> <a
          class="text-white underline hover:text-black" @click="toLogin">Log In</a></p></div>
      </div>

</template>
<script>
import api from '@/api';
import mixin from '@mixin';
import layer from '@/utils/layer';
import validate from '@/utils/validate';

export default {
  name: 'register',
  mixins: [mixin],
  data() {
    return {
      email: null,
      // username:null,
      password: null,
      password_verify: null,
    };
  },
  methods: {
    register() {
      const { email, password, password_verify } = this;
      const obj = {
        email, password, password_verify,
      };

      this.validResult = validate.isRequiredCheck(obj);

      if (!this.validResult.valid) {
        layer.tip(this.validResult.msg, 'error');
      } else {
        api.loginApi.register(obj).then((result) => {
          if (!result.success) {
            layer.tip('Please fill in the correct registration information.', 'error');
          } else {
            layer.tip('Register successfully !');
            setTimeout(() => {
              this.$router.push('/loginRegister/login');
            }, 1000);
          }
        });
      }
    },
  },

};
</script>
